@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


*{
    font-family: 'Open Sans', sans-serif;
}
body, html{
    height: 100%;
    overflow-x: hidden;
    scroll-behavior: smooth;
    
}

.wrapper{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: 
        "header"
        "hero"
        "dogsize"
        "footer";
    grid-template-rows: auto auto 1fr auto;
    min-height: 100vh;
}

.header {
    grid-area: header;
    grid-row: 1/2;
    box-shadow: 0 2px 6px 0 hsla(0, 0%, 0%, 0.8);
}

.hero{
    grid-area: hero;
    grid-row: 2/3;
    height: 90vh;
    background-image:linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.5)), url(/static/media/dog-hero.e12190a0.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
}

.dogsize{
    grid-area: dogsize;
    grid-row: 3/4;

}

.footer{
    grid-area: footer;
    grid-row: 4/5;
}

.footer-wrapper{
    display: grid;
    grid-template-columns: 15px 2fr 2fr 2fr 1fr 15px;
    grid-template-areas:
        ". mission mission mission mission ."
        ". mission mission mission mission ."
        ". social social social social ."
        ". copyright copyright copyright copyright ." ;
    background-color: #2e519e;
    box-shadow: 0 -4px 8px 0 hsla(0, 0%, 0%, 0.3);

}

.mission-state{
    grid-area: mission;
    text-align: center;
    margin-top: 10px;
    color: white;
    font-size: 1.25em;

}

.social-links{
    grid-area: social;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    border-top: solid white;
    border-bottom: solid white;
    padding: 10px 0 10px 0;
    color: white;

}

.copyright{
    margin-top: 15px;
    grid-area: copyright;
    text-align: center;
    color: white;
}



.header-nav{
    display: grid;
    grid-template-columns: auto 1fr 1fr;
}

.logo{
    -webkit-align-self: center;
            align-self: center;
    justify-self: center;
    margin-left: 20px;
}

.hamburger-menu{
    margin: 10px 20px 10px 10px;
    font-size: 3em;
    justify-self: flex-end;
 }

.reg-nav{
    visibility: hidden;
    
}

.title{
    display: none;
    
}



@media (min-width: 650px){
    .header-nav{
        display: grid;
        grid-template-columns: auto auto 1fr;
    }
    .hamburger-menu{
        display: none;
    }
    .ham-nav{
        display: none;
        
    }
    .reg-nav{
        visibility: visible ;
        justify-self: end;
    }
    .title{
        display: inline;
        display: initial;
        -webkit-align-self: center;
                align-self: center;
        margin-left: 10px;
        color: #2e519e;
    }

}
.hamnav-wrapper{
    box-shadow: 0 3px 8px 0 hsla(0, 0%, 0%, 0.4) inset;

}

.nav-choices{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.nav-choices > a:hover{
    color: grey;
}

.nav-choices > a{
    text-align: center;
    margin: 10px 0 10px 0;
    font-size: 1.5em;
    list-style-type: none;
    text-decoration: none;
    color: black;
    font-weight: bold;
}
.nav-choices-2{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    /* border: blue solid; */
}

.nav-choices-2 > a:hover{
    color: grey;
}

.nav-choices-2 > a{
    text-decoration: none;
    color: black;
    font-weight: bold;
    font-size: 1.5em;
    padding-left: 20px;
    padding-right: 20px;
    list-style-type: none;
}

.hero-wrapper{
    padding: 10px
}

.hero-wrapper > p{
    color: white;
    font-size: 1.25em;
    text-align: center;
    position: relative

}

.hero-wrapper > h1{
    color: white;
    font-size: 3em;
    text-align: center;
}


.chevron-down{
    position: absolute;
    bottom: 45px;
    font-size: 3em;
    right: 50%;
    color: white;
}

.chevron-down:hover{
    color: grey;
}
.ds-wrapper{
    display: grid;
    grid-template-columns:  1fr 1fr 1fr ;
    grid-template-areas: 
        " dog-header dog-header dog-header"
        " small-dog small-dog small-dog "
        " medium-dog medium-dog  medium-dog  "
        " large-dog large-dog  large-dog  ";
}

.dog-header{
    grid-area: dog-header;
    background-color: #2e519e;
    color: white;
    box-shadow: 0 4px 8px 0 hsla(0, 0%, 0%, 0.3);
}

.dog-header > h1{
    text-align: center;
    text-decoration: underline;
}

.dog > a {
    position: relative;
    z-index: 100;
}

.dog-header > p{
    text-align: center;
    width: auto;
    margin: 30px;
    font-size: 1.25em;

}

.dog > h1 {
    margin: 10px 0 5px 0;
}

.small-dog{
    padding-top: 15px;
    grid-area: small-dog;
    
}

.medium-dog{
    grid-area: medium-dog;
    border-top: solid black;
}

.large-dog{
    grid-area: large-dog;
    border-top: solid black;
}

@media (min-width: 1200px){
    .ds-wrapper{
        display: grid;
        grid-template-columns: 15px 1fr 1fr 1fr 15px;
        grid-template-areas: 
            "dog-header dog-header dog-header dog-header dog-header"
            ". small-dog medium-dog large-dog ."
            ". small-dog medium-dog  large-dog  ."
            ". small-dog medium-dog  large-dog  .";
    }
    
    .medium-dog{
        border-top: none;
        border-left: solid black;
        border-right: solid black;

    }
    
    .large-dog{
        border-top: none;
        
    }
}

.dog{
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: auto;
}
.container {
    border-radius: 50%;
    height: auto;
    -webkit-tap-highlight-color: transparent;
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
    transition: -webkit-transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1), -webkit-transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    width: 400px;
}
.container:hover {
    -webkit-transform: scale(.81);
            transform: scale(.81);
}
.container-inner {
    -webkit-clip-path: path(
      "M 390,400 C 390,504.9341 304.9341,590 200,590 95.065898,590 10,504.9341 10,400 V 10 H 200 390 Z"
    );
            clip-path: path(
      "M 390,400 C 390,504.9341 304.9341,590 200,590 95.065898,590 10,504.9341 10,400 V 10 H 200 390 Z"
    );
    position: relative;
    -webkit-transform-origin: 50%;
            transform-origin: 50%;
    top: -200px;
}
.circle {
    background-color: #fee7d3;
    border-radius: 50%;
    cursor: pointer;
    height: 380px;
    left: 10px;
    pointer-events: none;
    position: absolute;
    top: 210px;
    width: 380px;
}
.img {
    pointer-events: none;
    position: relative;
    -webkit-transform: translateY(20px) scale(1.15);
            transform: translateY(20px) scale(1.15);
    -webkit-transform-origin: 50% bottom;
            transform-origin: 50% bottom;
    transition: -webkit-transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1), -webkit-transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 2;
}
.container:hover .img {
    -webkit-transform: translateY(0) scale(1.2);
            transform: translateY(0) scale(1.2);
}
.img1 {
    top: 220px;
    right: 10px;
}
.img2 {
    top: 235px;
    right: 120px;
}
.img3 {
    top: 205px;
    right: 120px;
}

.dog-search-header{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: #2e519e;
    color: white;
    box-shadow: 0 4px 8px 0 hsla(0, 0%, 0%, 0.3);
}

.dog-search-header-title > h1{
    font-size: 3em;
    margin: 10px 0 10px 0;
}

.dog-results{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    padding: 30px;
}

.dog-cards{
    margin: 30px 30px 50px 30px;
    width: 350px;
    min-height: 450px;
    max-height: auto;
    box-shadow: 8px 8px 6px -6px #777;
    background-color: #2e519e;
    color: whitesmoke;
    border: solid rgb(219, 219, 219);
    transition: all 125ms ease-in-out;
    border-radius: 10px;
}

.dog-cards:hover{
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
}

.dog-cards-picture > img{
    max-width: 100%;
    max-height: 100%;
    border: solid  black;
    border-radius: 10px;
}

.dog-cards-inner{
    padding: 10px;
    margin: 15px;
    background-color: white;
    border-radius: 10px;
}


.dog-cards-breed{
    text-align: center;
    margin: 15px;
    font-size: 1.25em;
    font-weight: bold;
}


.dog-cards-desc{
    margin: 10px 10px 0 10px;
    color: black;
    overflow: auto;
    height: 180px;

}

.dog-cards-button{
    margin: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    
}

.go-up > a{
    position: absolute;
    bottom: 3%;
    right: 3%;
    font-size: 2.25em;
    color: black;

}

.go-up > a:hover{
    color: grey;
}

.no-choice{
    text-align: center;
}
.modal-wrapper{
    width: 450px;
    height: 700px;
    background-color: #2e519e;
    color: whitesmoke;
    border: solid rgb(219, 219, 219);
    border-radius: 10px;
    position: relative;
    top: 5%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.modal-wrapper{
    display: grid;
    grid-template-rows: auto 1fr;
    padding: 15px;
}

.modal-header{
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: 1fr;
    height: 40px;

}

.breed{
    grid-column: 1/7;
    grid-row-start: 1;
    text-align: center;
    padding-top: 5px;
    font-size: 1.25em;
    font-weight: bold;
    overflow: scroll;
}

.size-small{
    grid-column: 7/11;
    background-color: rgb(107, 161, 89);
    color: white;
    text-align: center;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    grid-row-start: 1;
    padding-top: 5px;
    font-size: 1.25em;
    font-weight: bold;
}
.size-medium{
    grid-column: 7/11;
    background-color: rgb(70, 112, 194);
    color: white;
    text-align: center;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    grid-row-start: 1;
    padding-top: 5px;
    font-size: 1.25em;
    font-weight: bold;
}
.size-large{
    grid-column: 7/11;
    background-color: rgb(186, 47, 60);
    color: white;
    text-align: center;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    grid-row-start: 1;
    padding-top: 5px;
    font-size: 1.25em;
    font-weight: bold;
}

.modal-body{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(auto, 1fr);
    color: black;
    background-color: white;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
}


.pic{
    grid-column: 1/7;
    grid-row-start: 1;
    padding: 10px 10px 0 10px;
}

.pic > img{
    width: 100%;
    border: solid rgba(0, 0, 0);
    border-radius: 5px;

}

.life{
    grid-column: 1/7;
    grid-row-start: 2;
    text-align: center;
    margin: 5px 0 5px 0;
}

.life > p, h4{
    display: inline;
}

.bio{
    grid-column: 1/7;
    grid-row-start: 3;
    margin: 0 10px 10px;
    padding: 10px;
    max-height: 175px;
    overflow: scroll;
    background-color: rgb(231, 231, 231);
    border: rgb(211, 211, 211) solid ;
    border-radius: 3px;
}

.height{
    grid-column: 1/7;
    grid-row-start: 4;
    margin: 0 15px 0 15px;
}

.height > p, h4{
    display: inline;
}

.weight{
    grid-column: 1/7;
    grid-row-start: 5;
    margin: 0 15px 0 15px;
}

.weight > p, h4{
    display: inline;
}




